* {
  margin: 0; 
  padding: 0;
  font-family: 'JetBrains Mono', monospace;
}

.home-divider {
    width: 100%;
    height: 10px;
    background: rgb(180, 179, 179);
}

@media only screen and (max-width: 600px) {
    .icons {
      flex-direction: column;
    }
  }

.social-link-button {
    border: none;
    background: none;
    color: none;
}

a {
    text-decoration: none;
}

.contact > * {
    font-size: 20px;
}

.card {
    font-size: 20px;
}

@media only screen and (max-width: 600px) {
    .card {
      width: 20rem!important;
    }
  }

